
'use server';

/**
 * @fileOverview A flow for transcribing audio to text.
 * - transcribeAudio - Transcribes audio data to text.
 * - TranscribeAudioInput - The input type for the transcribeAudio function.
 * - TranscribeAudioOutput - The return type for the transcribeAudio function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';
import { SpeechClient } from '@google-cloud/speech';

const TranscribeAudioInputSchema = z.object({
  audioDataUri: z
    .string()
    .describe(
      "A chunk of audio, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
  languageCode: z.string().default('en-US').describe('The language of the audio.'),
});
export type TranscribeAudioInput = z.infer<typeof TranscribeAudioInputSchema>;

const TranscribeAudioOutputSchema = z.object({
  transcript: z.string().describe('The transcribed text.'),
});
export type TranscribeAudioOutput = z.infer<typeof TranscribeAudioOutputSchema>;


export async function transcribeAudio(
  input: TranscribeAudioInput
): Promise<TranscribeAudioOutput> {
  return transcribeAudioFlow(input);
}


const transcribeAudioFlow = ai.defineFlow(
  {
    name: 'transcribeAudioFlow',
    inputSchema: TranscribeAudioInputSchema,
    outputSchema: TranscribeAudioOutputSchema,
  },
  async ({ audioDataUri, languageCode }) => {
    const speechClient = new SpeechClient();
    
    // The environment variable is automatically set by App Hosting.
    const projectId = process.env.GCLOUD_PROJECT;
    if (!projectId) {
        // In local development, this may not be set. Provide a clear message.
        console.warn("GCLOUD_PROJECT environment variable not set. Speech-to-text will only work when deployed to Firebase App Hosting.");
        throw new Error("GCLOUD_PROJECT environment variable not set. Please set it in your .env file for local development.");
    }
    
    const audioBytes = audioDataUri.split(',')[1];
    
    const request = {
        recognizer: `projects/${projectId}/locations/global/recognizers/_`,
        config: {
            model: 'chirp',
            languageCodes: [languageCode],
            features: {
              automaticPunctuation: true,
            },
        },
        content: audioBytes,
    };

    try {
        const [response] = await speechClient.recognize(request as any);
        const transcript = response.results
            ?.map(result => result.alternatives?.[0].transcript)
            .join('\n') || '';
        
        return { transcript };
    } catch (error) {
        console.error('Speech-to-text API error:', error);
        throw new Error('Failed to transcribe audio.');
    }
  }
);
