
"use client";

import { useState, useRef, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Send, Loader2, Mic, StopCircle, Speaker, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent } from "@/components/ui/card";
import { teachAnything } from "@/ai/flows/teach-anything";
import { transcribeAudio } from "@/ai/flows/speech-to-text";
import { textToSpeech } from "@/ai/flows/text-to-speech";
import { SahayakLogo } from "@/components/icons";
import { useToast } from "@/hooks/use-toast";
import { cn } from "@/lib/utils";

const suggestionPrompts = [
  {
    title: "Create a worksheet",
    prompt: "Create a math worksheet for grade 5 on multiplication",
    feature: "worksheets"
  },
  {
    title: "Generate a quiz",
    prompt: "Generate a science quiz about photosynthesis for grade 7",
    feature: "games"
  },
  {
    title: "Make a visual aid",
    prompt: "Make a visual aid showing the water cycle",
    feature: "visual-aids"
  },
  {
    title: "Design a game",
    prompt: "Design a word scramble game for vocabulary about animals",
    feature: "games"
  },
];

const featureKeywords: Record<string, string[]> = {
  worksheets: ['worksheet', 'exercise', 'practice sheet'],
  games: ['game', 'quiz', 'bingo', 'riddle', 'scramble', 'true/false', 'fill-in-the-blanks'],
  'visual-aids': ['visual', 'chart', 'diagram', 'image', 'drawing', 'table'],
  'knowledge-base': ['explain', 'what is', 'how do', 'why is'],
  'generate-content': ['story', 'poem', 'dialogue', 'write a'],
};

interface Message {
  id: number;
  text: string;
  isUser: boolean;
  audioDataUri?: string;
}

export function DashboardClient() {
  const router = useRouter();
  const { toast } = useToast();
  const [messages, setMessages] = useState<Message[]>([]);
  const [prompt, setPrompt] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  
  // Voice state
  const [isRecording, setIsRecording] = useState(false);
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [isPlayingAudio, setIsPlayingAudio] = useState<number | null>(null);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const audioChunksRef = useRef<Blob[]>([]);
  const audioPlayerRef = useRef<HTMLAudioElement | null>(null);

  const handleStartRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream, { mimeType: 'audio/webm; codecs=opus' });

      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };

      mediaRecorderRef.current.onstop = async () => {
        setIsRecording(false);
        setIsTranscribing(true);
        const audioBlob = new Blob(audioChunksRef.current, { type: 'audio/webm; codecs=opus' });
        const reader = new FileReader();
        reader.readAsDataURL(audioBlob);
        reader.onloadend = async () => {
          const base64Audio = reader.result as string;
          try {
            const { transcript } = await transcribeAudio({ audioDataUri: base64Audio, languageCode: 'en-IN' });
            setPrompt(transcript);
          } catch (error) {
            console.error('Transcription error:', error);
            toast({ variant: 'destructive', title: 'Transcription failed', description: 'Could not understand audio. Please try again.' });
          } finally {
            setIsTranscribing(false);
            audioChunksRef.current = [];
          }
        };
      };

      audioChunksRef.current = [];
      mediaRecorderRef.current.start();
      setIsRecording(true);
    } catch (error) {
      console.error('Error accessing microphone:', error);
      toast({ variant: 'destructive', title: 'Microphone access denied', description: 'Please allow microphone access in your browser settings.' });
    }
  };

  const handleStopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
    }
  };

  const detectFeature = (text: string) => {
    const lowerText = text.toLowerCase();
    for (const feature in featureKeywords) {
      for (const keyword of featureKeywords[feature]) {
        if (lowerText.includes(keyword)) {
          return feature;
        }
      }
    }
    return null;
  };
  
  const redirectToFeature = (feature: string, promptText: string) => {
    router.push(`/${feature}?prompt=${encodeURIComponent(promptText)}`);
  };

  const processPrompt = async (promptText: string) => {
    if (!promptText.trim()) return;

    setIsLoading(true);
    const userMessage: Message = { id: Date.now(), text: promptText, isUser: true };
    setMessages(prev => [...prev, userMessage]);
    setPrompt("");
    
    const matchedFeature = detectFeature(promptText);

    if (matchedFeature) {
        redirectToFeature(matchedFeature, promptText);
        return;
    }

    try {
      const result = await teachAnything({ query: promptText });
      const aiMessage: Message = { id: Date.now() + 1, text: result.response, isUser: false };
      setMessages(prev => [...prev, aiMessage]);
    } catch (error) {
      console.error("AI chat error:", error);
      const errorMessage: Message = { id: Date.now() + 1, text: "Sorry, I ran into a problem. Please try again.", isUser: false };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const handlePlayAudio = async (message: Message) => {
    if (isPlayingAudio === message.id) {
        if (audioPlayerRef.current) {
            audioPlayerRef.current.pause();
            audioPlayerRef.current.currentTime = 0;
        }
        setIsPlayingAudio(null);
        return;
    }
    
    setIsPlayingAudio(message.id);
    try {
      const { audioDataUri } = await textToSpeech({ text: message.text });
      if (audioDataUri) {
          const audio = new Audio(audioDataUri);
          audioPlayerRef.current = audio;
          audio.play();
          audio.onended = () => {
              setIsPlayingAudio(null);
          };
      }
    } catch (error) {
        console.error('TTS error:', error);
        toast({ variant: 'destructive', title: 'Could not play audio', description: 'Failed to generate speech. Please try again.' });
        setIsPlayingAudio(null);
    }
  };
  
  useEffect(() => {
    return () => {
        if (audioPlayerRef.current) {
            audioPlayerRef.current.pause();
        }
    }
  }, []);

  return (
    <div className="flex flex-col h-full w-full max-w-4xl mx-auto">
      <div className="flex-1 pb-24 overflow-y-auto">
        {messages.length === 0 ? (
          <div className="text-center py-12">
            <h1 className="text-4xl font-bold font-headline mb-4">Salute, Super Teacher! 👋</h1>
            <p className="text-muted-foreground text-lg">How can I assist you with your teaching today?</p>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-12">
              {suggestionPrompts.map((p) => (
                <Card 
                    key={p.title} 
                    className="p-4 text-left hover:bg-muted cursor-pointer transition-colors"
                    onClick={() => redirectToFeature(p.feature, p.prompt)}
                >
                    <CardContent className="p-0">
                      <h3 className="font-semibold">{p.title}</h3>
                      <p className="text-sm text-muted-foreground">{p.prompt}</p>
                    </CardContent>
                </Card>
              ))}
            </div>
          </div>
        ) : (
          <div className="space-y-6 pt-4">
            {messages.map((msg) => (
              <div key={msg.id} className={`flex gap-2 ${msg.isUser ? 'justify-end' : 'justify-start'}`}>
                {!msg.isUser && <SahayakLogo className="w-8 h-8 shrink-0" />}
                <div className={`px-4 py-3 rounded-2xl max-w-xl ${msg.isUser ? 'bg-primary text-primary-foreground rounded-br-none' : 'bg-muted rounded-bl-none'}`}>
                  <pre className="whitespace-pre-wrap font-body text-base">{msg.text}</pre>
                </div>
                {!msg.isUser && (
                   <Button size="icon" variant="ghost" className="shrink-0" onClick={() => handlePlayAudio(msg)} disabled={isPlayingAudio !== null && isPlayingAudio !== msg.id}>
                    {isPlayingAudio === msg.id ? <StopCircle className="animate-pulse text-primary" /> : <Speaker />}
                  </Button>
                )}
              </div>
            ))}
            {isLoading && (
               <div className="flex justify-start">
                 <SahayakLogo className="w-8 h-8 mr-2 shrink-0" />
                 <div className="px-4 py-3 rounded-2xl max-w-xl bg-muted rounded-bl-none flex items-center">
                    <Loader2 className="h-5 w-5 animate-spin"/>
                 </div>
               </div>
            )}
          </div>
        )}
      </div>
      
      <div className="fixed bottom-0 right-0 left-0 md:left-[14rem] bg-background/80 backdrop-blur-sm">
        <div className="max-w-4xl mx-auto px-4 py-4">
            {isRecording || isTranscribing ? (
                 <div className="relative w-full flex items-center justify-center p-4 border rounded-full bg-background">
                    {isRecording && <div className="absolute left-6 text-sm text-muted-foreground">Recording...</div>}
                    {isTranscribing && <div className="absolute left-6 text-sm text-muted-foreground">Transcribing...</div>}
                    <div className="h-2 w-2 bg-red-500 rounded-full animate-pulse"></div>
                    <Button
                        size="icon"
                        variant="destructive"
                        className="absolute right-2.5 top-1/2 -translate-y-1/2 rounded-full"
                        onClick={handleStopRecording}
                        disabled={isTranscribing}
                    >
                       {isTranscribing ? <Loader2 className="animate-spin" /> : <StopCircle />}
                    </Button>
                 </div>
            ) : (
                <div className="relative">
                    <Input
                      type="text"
                      placeholder="Ask me anything, or use the mic to speak..."
                      value={prompt}
                      onChange={(e) => setPrompt(e.target.value)}
                      onKeyDown={(e) => { if (e.key === 'Enter') processPrompt(prompt); }}
                      className="w-full p-6 border rounded-full focus:outline-none focus:ring-2 focus:ring-primary pr-24"
                      disabled={isLoading}
                    />
                    <div className="absolute right-2.5 top-1/2 -translate-y-1/2 flex items-center gap-1">
                       <Button
                          size="icon"
                          variant="ghost"
                          className="rounded-full"
                          onClick={handleStartRecording}
                          disabled={isLoading}
                        >
                            <Mic />
                        </Button>
                        <Button
                          size="icon"
                          className="rounded-full"
                          onClick={() => processPrompt(prompt)}
                          disabled={isLoading || !prompt.trim()}
                        >
                          {isLoading ? <Loader2 className="animate-spin" /> : <Send />}
                        </Button>
                    </div>
                </div>
            )}
        </div>
      </div>
    </div>
  );
}
